
ul {
  background-color: #ffffff;
  list-style-type: none;
  margin: 0;
  padding: 10px;
  overflow: hidden;
  text-align: center;
  border-bottom: 2px solid black;
}

li { 
  /* float: left; */
  display: inline-block;
  font-size: 1.5rem;

}
li a {
  display: block;
  background-color: white;
  color: black;
  padding: 0px 50px;
  text-decoration: none;
  text-align: center;
  font-weight: bold;
}
li a.current {
  background-color: black;
  color: white;
 
}
li a:hover:not(.current) {
  background-color: white;
  color: gray;
}

